$mainColor: red;

.navTitle {
	padding: 5px 0;
}
.my-alert {
	position: relative;
	background: rgb(255, 214, 139);
	padding: 20px;
	border-radius: 5px;
	max-width: 500px;
	width: 100%;
	margin: auto;
	p {
		margin-bottom: 0;
	}
}

/* 탭메뉴 */
.wow-enter {
	opacity: 0;
}
.wow-enter-active {
	opacity: 1;
	transition: all 0.2s;
}

.cart-alert-close {
	all: unset;
	position: absolute;
	top: 0;
	right: 0;
	transform: translateX(-50%);
}

.container > .btn-light {
	position: absolute;
	left: 0;
	background-color: rgb(248, 249, 250);
}

.recent {
	width: 33%;
	float: left;
	font-size: 0.7rem;
	h2 {
		font-size: 1rem;
		white-space: nowrap;
	}
	img {
		width: 50%;
		height: 50%;
	}
	::after {
		content: '';
		display: block;
		clear: both;
	}
	@media (max-width: 400px) {
		width: 100%;
		font-size: 0.8rem;
		h2 {
			font-size: 1rem;
		}
	}
}
